$bgcolor:#00ccff;
$fgcolor:#000000;
@mixin cen {
  display:flex;
  justify-content: center;
  align-items:center;
  flex-direction:column;
}
@mixin spinner($del,$bg) {
  position:absolute;
  bottom:-30%;
  left:-20%;
  width:500px;
  height:500px;
  background:$bg;
  border-radius:50%;
  clip-path: ellipse(47% 40% at 50% 50%);
  animation:spin 3s linear infinite;
  animation-delay: $del;
}
* {
  outline:none;
}
.MainWrapper {
  &.Notfound {
    z-index:1;
    color:$fgcolor;
  }
  overflow: hidden;
  background:$bgcolor;
  border-radius:30px;
  padding:1rem;
  @include cen;
  position:relative;
  & .form {
    z-index:1;
    display:flex;
    justify-content: center;
    align-items:center;
    padding:1rem;
    & .form-control {
      color:"#000000";
      padding:1rem;
      border-radius:30px;
      font-weight:bolder;
    }
    & button {
      border-radius:30px;
    }
  }
  & .dataWrapper {
    z-index:1;
    @include cen;
    & .cityWrapper {
      &h1 {
        padding:2rem;
        font-size:2.5rem;
        color:$fgcolor;
      }
    }
    & .data {
      @include cen;
      & .main_temp {
        padding:1rem;
        font-size:2rem;
        color:$fgcolor;
      }
      & .Temps {
        @include cen;
        border-radius:50px;
        & span,h1 {
          color:$fgcolor;
          padding:1.5rem;
        }

      }
    }
  }
}
.AnimatedCircle {
  .Circles1 {
    @include spinner(1.2s,rgba(255,255,255,0.5));
  }
  .Circles2 {
    @include spinner(0.7,rgba(255,255,255,0.738));
  }
  .Circles3 {
    @include spinner(0.2,rgba(255,255,255,0.408));
  }
}
@keyframes spin {
  from {
    transform:rotate(0deg);
  }
  to {
    transform:rotate(-360deg);
  }
}