.showItems {
  margin-top: 3rem;
  text-align: center;
  min-width:200px;
  max-width:300px;
  max-height:200px;
  overflow:scroll;
}

.eachItem {
  background: #fff;
  padding: 1.2rem 1rem;
  border-radius: 0.5rem;
  margin-bottom: 0.7rem;
  min-width: 200px;
  word-break: break-word;
  background: rgb(85, 41, 220);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.eachItem:hover {
  background: rgb(249, 249, 252);
  color: rgb(85, 41, 220);
}

.showItems .eachItem h3 {
  font-size: 1.6rem;
  color: #fff;
}
.showItems .eachItem h3.Comp{
  text-decoration: line-through;
  color:rgb(102,102,102);
}
.fa-trash-alt,
.fa-edit,.fa-line {
  /* margin-left: -2.5rem; */
  cursor: pointer;
  pointer-events: auto;
  z-index: 10;
  color: rgb(251, 251, 251);
  transition: color 0.15s linear;
  font-size: 2rem;
}

.addItems .fa-edit,.fa-line {
  color: rgb(47, 214, 122);
}


/*  Please Subscribe to ThapaTechnical Youtube Channel
/* Click Here: https://www.youtube.com/thapatechnical  */

.eachItem:hover .fa-trash-alt {
  color: rgb(214, 47, 61);
}
.eachItem .fa-edit{
  color: rgb(12,183,255);
}
.eachItem:hover .fa-line {
  color: rgb(12,183,255);
}

.eachItem:hover h3 {
  color: rgb(85, 41, 220);
}

.btn {
  width:auto;
  position: relative;
}
.btn:hover {
  text-decoration: none;
}

.effect04 {
  letter-spacing: 0.1em;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  line-height: 45px;
  text-decoration: none;
  text-transform: uppercase;
  --uismLinkDisplay: var(--smLinkDisplay, inline-flex);
  display: var(--uismLinkDisplay);
  color: #000;
  outline: solid 2px #000;
  position: relative;
  transition-duration: 0.4s;
  overflow: hidden;
  -webkit-backface-visibility:hidden;
          backface-visibility:hidden;
}

.effect04::before,
.effect04 span {
  margin: 0 auto;
  transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
  transition-duration: 0.4s;
}

.effect04:hover {
  background-color: rgb(255,167,40);
  text-decoration: none;
}

.effect04:hover span {
  
  -webkit-transform: rotateY(-90deg) scale(-0.1, 20);
  transform: rotateY(-90deg) scale(-0.1, 20);
}

.effect04::before {
  background-color: rgb(255,167,40);
  height:100%;
  content: attr(data-sm-link-text);
  color: #fff;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  display:grid;
  place-items: center;
  -webkit-transform: rotateY(90deg) scale(-0.1, 20);
  transform: rotateY(90deg) scale(-0.1, 20);
}

.effect04:hover::before {
  letter-spacing: 0.05em;
  -webkit-transform: rotateY(0) scale(1, 1);
  transform: rotateY(0) scale(1, 1);
}
