.showItems {
  margin-top: 3rem;
  text-align: center;
  min-width:200px;
  max-width:300px;
  max-height:200px;
  overflow:scroll;
}

.eachItem {
  background: #fff;
  padding: 1.2rem 1rem;
  border-radius: 0.5rem;
  margin-bottom: 0.7rem;
  min-width: 200px;
  word-break: break-word;
  background: rgb(85, 41, 220);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.eachItem:hover {
  background: rgb(249, 249, 252);
  color: rgb(85, 41, 220);
}

.showItems .eachItem h3 {
  font-size: 1.6rem;
  color: #fff;
}
.showItems .eachItem h3.Comp{
  text-decoration: line-through;
  color:rgb(102,102,102);
}
.fa-trash-alt,
.fa-edit,.fa-line {
  /* margin-left: -2.5rem; */
  cursor: pointer;
  pointer-events: auto;
  z-index: 10;
  color: rgb(251, 251, 251);
  -webkit-transition: color 0.15s linear;
  transition: color 0.15s linear;
  font-size: 2rem;
}

.addItems .fa-edit,.fa-line {
  color: rgb(47, 214, 122);
}


